@import url(https://fonts.googleapis.com/css?family=Roboto:400,700&subset=latin-ext);
* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}
html{
    font-family: 'Roboto', sans-serif;
}


.calc{
    height: 500px;
    width: 500px;
    margin: 20vh auto 0;
    border: 5px solid #444;
    display: -webkit-flex;
    display: flex;
    -webkit-flex-direction: column;
            flex-direction: column;
}
.txt{
    box-sizing: border-box;
    padding: 20px 30px;
    text-align: right;
    word-break: break-all;
    overflow: hidden;
    font-family: Arial;
}
.result{
    height: 100px;
    font-size: 30px;
    color: #555;
}
.input{
    height: 80px;
    font-size: 25px;
    border-bottom: 5px solid #444;
    border-top: 5px dotted #444;
}
button{
    box-sizing: border-box;
    background-color: #ddd;
    border: 3px solid black;
    font-size: 30px;
    font-weight: bold;
    cursor: pointer;
}
button:focus{
    outline: none;
}
.interface{
    display: -webkit-flex;
    display: flex;
    -webkit-flex-wrap: wrap;
            flex-wrap: wrap;
    -webkit-flex-grow: 1;
            flex-grow: 1;
}

.btn-container{
    display: -webkit-flex;
    display: flex;
    -webkit-justify-content: space-around;
            justify-content: space-around;
    justify-items: space-around;
    -webkit-flex-wrap: wrap;
            flex-wrap: wrap;
}

.basics{
    -webkit-flex-direction: column;
            flex-direction: column;
    width: calc(100% / 4);
    height: 80%;
}
.basics button{
    width: calc(100% - 10px);
    height: calc(25% - 10px);
}

.basics1{
    width: 100%;
    height: 20%;
}
.basics1 button{
    width: calc(25% - 10px);
    height: calc(100% - 10px);
}
.numbers{
    width: calc(100% * 3 / 4);
    height: 80%;
}

.numbers button{
    width: calc(33% - 10px);
    height: calc(25% - 10px);
    
}
button{
    margin: 5px;
}
footer{
    position: fixed;
    bottom: 0;
    left: 0;
    right: 0;
    height: 40px;
    line-height: 37px;
    text-align: center;
    font-weight: bold;
    background-color: grey;
    border-top: 3px solid black;
}
nav.mainNav{
    max-width: 600px;
    margin: 0 auto;
    height: 80px;
    display: -webkit-flex;
    display: flex;
    -webkit-justify-content: space-around;
            justify-content: space-around;
    -webkit-align-items: center;
            align-items: center;
}

.navDiv{
    width: 100vw;
    background-color: #eee;
    border-bottom: 3px dashed black;
}

.mainNav a{
    height: 50px;
    line-height: 30px;
    text-decoration: none;
    color: black;
    font-weight: bold;
    text-transform: uppercase;
    cursor: pointer;
    background-color: #ddd;
    padding: 10px 15px;
    border: 2px solid black;
    transition: .3s;
}

.mainNav a:hover{
    border-bottom-width: 6px;
    background-color: #fff;
}

a.active{
    border-width: 4px;
    padding: 8px 13px;
}

a.blue{
    border-color: blue;
}
a.orange{
    border-color: orange;
}
a.green{
    border-color: green;
}
.contact{
    margin-top: 100px;
}

.contact p{
    text-align: center;
    font-size: 30px;
}
.contact .contactTxt{

}
.contact .mail{
    font-size: 40px;
    font-weight: bold;
    letter-spacing: 1px;
}
a i{
    color: black;
    font-size: 50px;
    transition: .3s;
    margin-left: 15px;
}
a i:hover{
    color: darkgoldenrod;
}
.workInProgress{
    font-size: 40px;
    text-align: center;
    margin: 100px auto 0;
}
